import { useState } from "react";
import { Button } from "antd";
import { Link } from "react-router-dom";
import { useEffect } from "react";

function Home() {

  const [isLogin, setIsLogin] = useState(false);

  useEffect(() => {
    setIsLogin(localStorage.getItem("isLogin") === "true");
  }, []);

  const onLogin = () => {
    setIsLogin(true);
    localStorage.setItem("isLogin", true);
  }

  const onLogout = () => {
    setIsLogin(false);
    localStorage.removeItem("isLogin");
  }

  return (
    <div>
      <div>Home </div>
      <div>
        <div>
          <span><Link className="mr-2" to="/a">PageA</Link></span>
        </div>
        <div>
          <span><Link to="/b">PageB</Link></span>
        </div>
        <div>
          <div>{`${isLogin}`}</div>
          {isLogin ?
            <Button type="primary" ghost danger onClick={onLogout}></Button> :
            <Button type="primary" onClick={onLogin}></Button>
          }
        </div>
      </div>
    </div>
  )
}

export default Home;